import noData from '@/components/noData'
import back from '@/components/back'
export default {
    components:{
        noData,
        back,
    },
    data(){
        return{
			user: this.$store.state.userInfo,
			page: 1,
			haveMore: false,
			search:'',
			picArr:this.$store.state.picArr,
			tNum:0,
			ImgStatusList:[],
            SubjectBuyRecordId:0,
        }
    },
    created(){
        this.$bridge.callhandler('getSubjectBuyRecordId','',(data) => {
        	this.SubjectBuyRecordId = data
            responseCallback('接收到方法')
        })
    },
    mounted(){
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
		imgClick(i,idx){
			const data = {
			    position: idx,
			    url: [i.ImagePath]
			}
			this.$bridge.callhandler('Preview', JSON.stringify(data))
		},
		ChooseIcon(i,idx){
			this.picArr.forEach((v,k) => {
				if(i.MedicalImgId === v.MedicalImgId){
					if(i.ImgStatus){
						this.picArr[k].ImgStatus = 0
					}else{
						this.picArr[k].ImgStatus = 1
					}
				}

			})
			this.totalNum()
		},
		totalNum(){
			this.tNum = 0
			this.ImgStatusList = []
			this.picArr.forEach((v,k) => {
				if(v.ImgStatus > 0){
					this.tNum += 1
					var Arr = {
						MedicalImgId:v.MedicalImgId,
						ImgStatus:v.ImgStatus
					}
					this.ImgStatusList.push(Arr)
				}
			})
		},
		ChangeMedicalImg(){
			if(this.ImgStatusList && this.ImgStatusList.length){
				this.$post('MDTAPI/MDTMedical/ChangeMedicalImgStatus',{
					ImgStatusList:this.ImgStatusList,
                    SubjectBuyRecordId:this.SubjectBuyRecordId,
				}).then(o => {
					if(o){
						this.$toast("标记成功")
						this.$router.back()
					}

				})
			}else{
				this.$toast("请标记图片")
			}
		},
        goback(){
            this.$router.back()
        },

    }
}



